//

import Vuex from 'vuex'
export default {
	onLaunch: function () {
		console.log('App Launch')
		this.audio.onEnded(() => {
			if (this.mode === 1){
				//单曲循环
				this.play()
			}else if (this.mode === 2) {
				this.setIndex(1)
				let src = this.history[this.musicIndex].src;
				let detail = this.history[this.musicIndex].detail;
				this.setSrc(src);
				this.setDetail(detail)
			}else  if (this.mode === 3) {
				let index = Math.floor(Math.random() * this.history.length);
				let src = this.history[index].src;
				let detail = this.history[index].detail;
				this.setSrc(src);
				this.setDetail(detail)
			};
		})
	},
	onShow: function () {
		console.log('App Show')
	},
	onHide: function () {
		console.log('App Hide')
	},
	computed: {
		...Vuex.mapState(['audio', 'mode', 'musicIndex', 'history', 'detail'])
	},
	methods: {
		...Vuex.mapMutations(['play', 'setSrc', 'setDetail', 'setIndex'])
	}
}
